<template>
	<view class="content">
		<!-- #ifdef APP-PLUS -->
			<view class="statusBox"></view>
		<!-- #endif -->
		
		<view class="bg_title">
			<image class="QR_icon" src="../../static/image/index/icon_18.png" mode=""    @tap="btnQR"></image>
			<image class="User_icon" src="../../static/image/index/icon_19.png" mode=""	@tap="goUser"></image>
			<view class="titleNames">叮叮挪车</view>
		</view>
				
		<image class="Agent_icon" src="../../static/image/index/icon_66.png" mode="" @tap="goAgent"></image>
		<uni-swiper-dot class="swiper" :info="bannerList" :current="current" mode="dot" :dotsStyles="dotsStyles" @tap="btnQR" style="margin-top: 90upx;">
			<swiper class="swiper-box swiper" @change="change" :autoplay="autoplay" circular="true">
				<swiper-item v-for="(item, index) in bannerList" :key="index">
					<image class="swiperitem banner"  :src="item.src"/>  
				</swiper-item>
			</swiper>  
		</uni-swiper-dot>
		<view class="swiperDown">
			<view class="titleA fontSize12">手机扫一扫 联系车主挪车</view>
			<view class="titleB fontSize12"  @tap="BtnAskFor">立即免费体验</view>
		</view>
		
		<view class="titleBox">
			<image class="icons" src="../../static/image/index/icon_3.png" mode=""></image>
			<view class="titleName fontSize15">购买挪车码</view>
			<view class="clearBoth"></view>
		</view>
		
		<view class="maxBox">
			<view class="scrolllBox" :style="{width:scrollWidth}">
				<view class="scroll" v-for="(item,index) in scroolBox" :key="index" @tap="inSells(item.id)">
					<image class="sellImage" :src="item.src" mode=""></image>
					<view class="sellTitle fontSize12 overflow_one">{{item.titleName}}</view>
					<view class="priceIcon fontSize12">￥</view>
					<view class="price">{{item.price}}</view>
					<s class="del fontSize12">￥{{item.priceBefore}}</s>
				</view>
			</view>
		</view>
		<view class="clearBoth"></view>
		<!-- <view class="titleBox">
			<image class="icons" src="../../static/image/index/icon_3.png" mode=""></image>
			<view class="titleName fontSize15">购买挪车码</view>
			<view class="clearBoth"></view>
		</view> -->
		
		<view class="maxBox">
			<view class="scrolllBox">
				<view class="scroll addPro" v-for="(item,index) in scroolBoxs" :key="index" @tap="inSells(item.id)">
					<image class="sellImage" :src="item.src" mode=""></image>
					<view class="sellTitle fontSize12 overflow_one">{{item.titleName}}</view>
					<view class="priceIcon fontSize12">￥</view>
					<view class="price">{{item.price}}</view>
					<s class="del fontSize12">￥{{item.priceBefore}}</s>
				</view>
			</view>
		</view>
		<view class="clearBoth"></view>
		
		<view class="commentBox">
			<view class="title">
				<view class="icons"></view>
				<view class="name fontSize14">商品评价</view>
				<view class="allComment fontSize12" @tap="allComment">查看全部<uni-icons class="icon_right" type="arrowright" color="rgba(0,0,0,0.4)" size="12"/></view>
			</view>
			<view class="clearBoth"></view>
			<view class="commentData" v-for="(item,index) in commentData" :key="index">
				<image class="headImage" :src="item.headImage" mode=""></image>
				<view class="name fontSize14 overflow_four">{{item.name}}</view>
				<view class="clearBoth"></view>
				<view class="contents overflow_two fontSize13">{{item.comments}}</view>
				<image class="showImage" :src="items" mode="" v-for="(items,indexs) in item.showImage" :key="indexs" @tap="thisImage(items)"></image>
				<view class="clearBoth"></view>
			</view>
			<view class="clearBoth"></view>
		</view>
		
		<view class="lasts" v-html="lastFooter"></view>
		<!-- <image class="lastImage" src="../../static/image/index/last.png" mode="widthFix"></image> -->

		<view class="shadow" v-if="showShadow">
			<view class="close" @tap="closeShadow"></view>
			<view class="bg_white">
				<image class="post" :src="poster" mode="widthFix"></image>
				<image class="close_icon" src="../../static/image/index/icon_8.png" mode="widthFix" @tap="closeShadow"></image>
			</view>
		</view>
		
		<view class="addrShadow" v-if="addrShadows">
			<view class="close" @tap="closeAddrShadow"></view>
			<view class="bg_white">
				<view class="box">
					<picker @change="bindPickerChange" :value="index" :range="array" range-key="name">
						<image class="image" src="../../static/image/index/icon_14.png" mode=""></image>
						<view class="inputBox fontSize13">{{array[index].name}}</view>
						<uni-icons class="icons" type="arrowright" color="rgb(197,197,197)" size="12"/>
						<view class="clearBoth"></view>
					</picker>
				</view>
				<view class="clearBoth"></view>
				<view class="box">
					<picker @change="bindPickerChange2" :value="index2" :range="array2" range-key="name">
						<image class="image" src="../../static/image/index/icon_15.png" mode="" style="width:40upx;height:30upx"></image>
						<view class="inputBox fontSize13">{{array2[index2].name}}</view>
						<uni-icons class="icons" type="arrowright" color="rgb(197,197,197)" size="12"/>
						<view class="clearBoth"></view>
					</picker>
				</view>
				<view class="clearBoth"></view>
				<view class="box">
					<picker @change="bindPickerChange3" :value="index3" :range="array3" range-key="name">
						<image class="image" src="../../static/image/index/icon_16.png" mode=""></image>
						<view class="inputBox fontSize13">{{array3[index3].name}}</view>
						<uni-icons class="icons" type="arrowright" color="rgb(197,197,197)" size="12"/>
						<view class="clearBoth"></view>
					</picker>
				</view>
				<view class="clearBoth"></view>
				<view class="sure" @tap="getCity">提交</view>
			</view>
		</view>
		
		
	</view>
</template>
<style>
	.statusBox{width:100%;height:90upx;background: white;position: fixed;top:0;left: 0;z-index: 5;}
	.content{width:100%;padding:0;background: rgb(244,244,244);overflow-x: hidden;margin-top: 90upx;}
	.content .bg_title{width:100%;height:90upx;line-height: 90upx;position: absolute;top:90upx;z-index: 3;background: white;}
	.content .QR_icon{width:38upx;height:37upx;position: absolute;top:30upx;left:5%;z-index:2;}
	.content .titleNames{width:100%;height:37upx;text-align: center;font-weight: bold;font-size: 21px;position: absolute;top:0;left:0%;z-index:2;color:rgb(34,139,245)}
	.content .User_icon{width:38upx;height:42upx;position: absolute;top:30upx;right:5%;z-index:2;}
	
	.content .Agent_icon{width:150upx;height:150upx;position: fixed;top:85%;right:5%;z-index:2;}
	.content .swiper{width:100%;height:540upx;}
	.content .banner{width:100%;height:540upx;}
	.content .swiperDown{width:100%;height:220upx;position: relative;top:-35upx;background: url(../../static/image/index/bg_1.png);background-repeat: no-repeat;background-size: 100% 100%;}
	.content .swiperDown .titleA{position: absolute;top:30%;right:5%;color:white;}
	.content .swiperDown .titleB{position: absolute;top:55%;right:10%;color:white;background: white;color:#4A70C3;padding:10upx 20upx;border-radius: 5upx;}
	.content .titleBox{width:90%;;padding:2.5%;margin:0upx 0;;margin-left:2.5%;}
	.content .titleBox .icons{width:24upx;height:24upx;;float: left;position: relative;top:10upx;}
	.content .titleBox .titleName{float: left;color:#202020;margin-left: 20upx;font-weight: bold;}
	.content .maxBox{width:95%;margin-left: 5%;overflow-x: scroll;}
	.content .maxBox .scrolllBox{width:auto;}
	.content .maxBox .scrolllBox .scroll{width:300upx;padding-bottom: 20upx;background: white;border-radius:15upx;float: left;margin-right:20upx}
	.content .maxBox .scrolllBox .scroll .sellImage{width:100%;height:375upx;border-radius: 10upx;}
	.content .maxBox .scrolllBox .scroll .sellTitle{color:rgba(0,0,0,0.6);margin-bottom:10upx;width:100%;padding-left:10upx}
	.content .maxBox .scrolllBox .scroll .priceIcon{float:left;position: relative;top:10upx;color:#E72B33;padding-left:10upx}
	.content .maxBox .scrolllBox .scroll .price{float:left;color:#E72B33;font-size: 17px;}
	.content .maxBox .scrolllBox .scroll .del{float:left;margin-left: 30upx;color:#909090;position: relative;top:10upx;}
	.content .maxBox .scrolllBox .addPro{width:30%;margin-top: 20upx;}
	.content .maxBox .scrolllBox .addPro .sellImage{height:200upx}
	
	/* .content .commentBox{width:95%;padding:5% 2.5%;padding-top: 30%;margin-top:80upx;position: relative;top:-80upx;background: white;position: relative;background: url(../../static/image/index/bg_2.png);background-repeat: no-repeat;background-size: 110% 180%;} */
	.content .commentBox{width:95%;padding:5% 2.5%;;margin-top:80upx;background: white;position: relative;background:white}
	.content .commentBox .title{width:100%;}
	.content .commentBox .title .icons{width:4upx;height:25upx;background: rgb(78,155,244);float: left;position: relative;top:8upx}
	.content .commentBox .title .name{float: left;margin-left: 10upx;}
	.content .commentBox .title .allComment{float: right;color:rgba(0,0,0,0.4);}
	.content .commentBox .title .icon_right{float: right;}
	.content .commentBox .commentData{width:90%;margin-left: 2.5%;padding-right: 5%;margin-top:40upx;margin-bottom: 20upx;border-bottom: 2upx solid #E9E9E9;padding-bottom: 60upx;}
	.content .commentBox .commentData .headImage{width:60upx;height:60upx;border-radius:60upx;margin-right: 20upx;float: left;}
	.content .commentBox .commentData .name{color:#8E8E8E;position: relative;top:10upx;width:80%;}
	.content .commentBox .commentData .contents{color:#222222;margin-top:10upx;width:100%;}
	.content .commentBox .commentData .showImage{width:30%;height:196upx;border-radius: 10upx;margin-top:20upx;margin-right:3.3%;float: left;}
	.content .shadow{width:100%;height:100%;position: fixed;top:0;left:0;z-index: 2;}
	.content .shadow .close{width:100%;height:100%;position: fixed;top:0;left:0;z-index: 3;background: rgba(0,0,0,0.6);}
	.content .shadow .bg_white{width:100%;height:100%;position: fixed;top:0;left:0;z-index: 4;}
	.content .shadow .bg_white .post{width:70%;position: absolute;top:20%;left:15%;z-index: 5;}
	.content .shadow .bg_white .close_icon{width:10%;position: absolute;top:70%;left:45%;z-index: 5;}
	.content .lastImage{width:100%;}
	.content .addrShadow{width:100%;height:100%;position: fixed;top:0;left:0;z-index: 2;}
	.content .addrShadow .close{width:100%;height:100%;position: fixed;top:0;left:0;z-index: 2;background: rgba(0,0,0,0.6);}
	.content .addrShadow .bg_white{width:70%;padding:5%;border-radius: 10upx;position: fixed;top:20%;left:10%;z-index: 4;background: white;;}
	.content .addrShadow .bg_white .image{width:33upx;height:32upx;float: left;position: relative;top:2upx;margin-right:20upx;}
	.content .addrShadow .bg_white .inputBox{color:#A0A0A0;float: left;}
	.content .addrShadow .bg_white .icons{color:#A0A0A0;float: right;position: relative;top:2upx;}
	.content .addrShadow .bg_white .box{margin:60upx 0;}
	.content .addrShadow .bg_white .sure{margin-top: 100upx;width:80%;padding:3% 5%;margin-left: 5%;border-radius: 10upx;text-align: center;color:white;background: rgb(78,178,244);}
	/* #ifdef APP-PLUS */
		.content{width:100;padding:0;background: rgb(244,244,244);margin-top: 90upx;}
	/* #endif */
</style>
<script>
	import uniIcons from '@/components/uni-icons/uni-icons.vue'
	import uniSection from '@/components/uni-section/uni-section.vue'
	import uniSwiperDot from '@/components/uni-swiper-dot/uni-swiper-dot.vue'
	export default {
		components:{
			uniIcons,
			uniSection,
			uniSwiperDot
		},
		data() {
			return {
				autoplay: true,
				interval: 2000,
				duration: 500,
				current: 0,
				bannerList:[],
				dotsStyles:{  
					bottom:10,
					backgroundColor: 'rgba(255,255,255,0.5)',
					border: '1px none solid',
					color: '#fff',
					selectedBackgroundColor: 'white',  
					selectedBorder: '1px none solid'
				},
				scroolBox:[],
				scroolBoxs:[],
				commentData:[],
				lastFooter:"",
				showShadow:false,
				addrShadows:false,
				arrayProvince:[],
				array:[
					{name:'请选择你所属的市',"id":0},
				],
				array2:[
					{name:'请选择你所属的县/区(选填)',"id":0},
				],
				array3:[
					{name:'请选择你所属的镇(选填)',"id":0},
				],
				arrayPro:[
					{name:'请选择你所属的镇(选填)',"id":0},
				],
				index: 0,
				index2: 0,
				index3: 0,
				indexProvince:0,
				poster:"",
				_$token:"",
				hrefs:"",
				locationProvince:"",
				scrollWidth:"",
				scrollWidths:"",
			}
		},
		onShow(){
			var that = this;
			try {
			    const value = uni.getStorageSync('token');
			    if (value) {
					that._$token = value
			    }else{
					try {
						uni.setStorageSync('userInfo', {"token":""});
					}catch (e){}
				}
			}catch(e){}
			this.bannerList = []
			this.scroolBox = []
			this.commentData = []
			this.arrayProvince = []
			this.array = [
				{name:'请选择你所属的市',"id":0},
			],
			that.getStorage();
			setTimeout(function(){
				that.GetProvince()
				that.AjaxHome();
				that.AjaxIsHaveAddr();
				that.AjaxAddrs();
				that.AjaxGetPro();
			},100)
		},
		onLoad(){
			var that = this;
		},
		onPullDownRefresh() {
			var that = this;
			this.bannerList = []
			this.scroolBox = []
			this.commentData = []
			this.arrayProvince = []
			this.array = [
				{name:'请选择你所属的市',"id":0},
			],
			that.GetProvince()
			that.AjaxHome();
			that.AjaxIsHaveAddr();
			that.AjaxAddrs();
			that.AjaxGetPro();
			uni.stopPullDownRefresh()
		},
		methods: {
			getStorage(){
				var that = this;
				try {
					const value = uni.getStorageSync('token');
					if (value) {
						that._$token = value
					}else{
						uni.showToast({icon:"none",title:"请先登录"})
						setTimeout(function(){
							uni.navigateTo({url:"../login/login"})
						},1500)
					}
				} catch (e) {}
			
			},
			AjaxHome(){
				var that = this;
				var bannerList = [],goodsList = [],commentList = []
				let param={
					"user_id":this._$token.id,
					"user_token":this._$token.token,
				};
				this._$InitRequest.request('Index/index', param).then(res => {
					res.data.data.top_banner.forEach( (item,index) =>{
						bannerList.push({
							id:item.id,
							src:item.cover_url
						})
					})
					for (var i = 0; i < bannerList.length; i++){
						that.bannerList.push(bannerList[i])
					}
					
					res.data.data.goods.forEach( (item,index) =>{
						goodsList.push({
							id:item.id,
							src:item.thumb_url,
							titleName:item.goods_name,
							price:item.shop_price,
							priceBefore:item.market_price,
						})
					})
					for (var i = 0; i < goodsList.length; i++){
						that.scroolBox.push(goodsList[i])
					}
					that.scrollWidth = that.scroolBox.length * 355 + "upx"
					
					res.data.data.comment.forEach( (item,index) =>{
						commentList.push({
							id:item.id,
							headImage:item.headimgurl,
							name:item.nickname,
							comments:item.content,
							showImage:item.pics
						})
					})
					for (var i = 0; i < commentList.length; i++){
						that.commentData.push(commentList[i])
					}
					
					that.lastFooter = res.data.data.bottom_text

				},error=>{})
			},
			AjaxGetPro(){
				var that = this;
				var list = []
				that.scroolBoxs = []
				let param={
					"user_id":this._$token.id,
					"user_token":this._$token.token,
				};
				this._$InitRequest.request('Index/goods_list', param).then(res => {
					
					res.data.data.forEach( (item,index) =>{
						list.push({
							id:item.id,
							src:item.thumb_url,
							titleName:item.goods_name,
							price:item.shop_price,
							priceBefore:item.market_price,
						})
					})
					for (var i = 0; i < list.length; i++){
						that.scroolBoxs.push(list[i])
					}
					that.scrollWidths = that.scroolBoxs.length * 355 + "upx"
				
				},error=>{})
			},
			GetProvince(){
				var that = this;
			},
			AjaxIsHaveAddr(){
				var that = this;
				let param={
					"user_id":this._$token.id,
					"user_token":this._$token.token,
				};
				this._$InitRequest.request('user/judge_city', param).then(res => {
					if( res.data.code !== 200 ){
						this.addrShadows = true
					}
				},error=>{})
			},
			AjaxAddrs(){
				var that = this;
				var lists = []
				var listProvince = []
				this.array = [
					{name:'请选择你所属的市',"id":0},
				]
				let param={
					"user_id":this._$token.id,
					"user_token":this._$token.token,
					"type":1
				};
				this._$InitRequest.request('index/get_city', param).then(resA => {
					resA.data.data.forEach((item,index) =>{
						listProvince.push({
							id:item.id,
							code:item.province_code,
							name:item.province_name
						})
					})
					for (var i = 0; i < listProvince.length; i++){
						that.arrayProvince.push(listProvince[i])
					}
					uni.getLocation({
					    type: 'gcj02',
						geocode:true,
					    success: function (resB) {
							that.locationProvince = resB.address.province
							that.arrayProvince.forEach((item,index)=>{
								if( item.name == that.locationProvince ){
									that.indexProvince = index
									let params={
										"user_id":that._$token.id,
										"user_token":that._$token.token,
										"type":2,
										"province_code":that.arrayProvince[that.indexProvince].code
									};
									
									that._$InitRequest.request('index/get_city', params).then(resC => {
										resC.data.data.forEach((item,index) =>{
											lists.push({
												id:item.id,
												code:item.city_code,
												name:item.city_name
											})
										})
										for (var i = 0; i < lists.length; i++){
											if( resB.address.city == lists[i].name ){
												that.array.push(lists[i])
											}
										}
									},error=>{})
								}
							})
					    }
					});
				},error=>{})
			},
			change(e){
				this.current = e.detail.current
			},
			inSells(index){
				uni.navigateTo({url:"./details/details?id=" + index})
			},
			BtnAskFor(){
				uni.navigateTo({url:"./askFor/askFor"})
			},
			btnQR(){
				var that = this;
				var ids = "";
				uni.scanCode({
				    success: function (res) {
						ids = res.result.split("goods_order_id=")[1]
						that.hrefs = res.result
						let param={
							"user_id":that._$token.id,
							"user_token":that._$token.token,
							"goods_order_id":ids
						};
						that._$InitRequest.request('index/check_code', param).then(res => {
							if( res.data.code !== 200 ){
								uni.showToast({icon:"none",title:res.data.mess})
								setTimeout(function(){
									uni.navigateTo({url:"./bindCard/bindCard?id=" + ids })
								},1500)
								return;
							}else{
								plus.runtime.openURL(that.hrefs, function(res){});
							}
						},error=>{})
				    }
				});
			},
			goUser(){uni.navigateTo({url:"../user/user"})},
			goAgent(){
				uni.navigateTo({url:"./agent/agent"})
			},
			allComment(){ uni.navigateTo({url:"../user/allComment/allComment"}) },
			closeShadow(){this.showShadow = false},
			bindPickerChange: function(e) {
				var that = this;
				var lists = []
				this.index = e.detail.value
				
				this.array2 = [
					{name:'请选择你所属的县/区(选填)',"id":0},
				]
				this.array3 = [
					{name:'请选择你所属的镇(选填)',"id":0},
				]
				this.index2 = 0;
				this.index3 = 0;
				
				let param={
					"user_id":this._$token.id,
					"user_token":this._$token.token,
					"type":3,
					"city_code":this.array[this.index].code
				};
				this._$InitRequest.request('index/get_city', param).then(res => {
					res.data.data.forEach((item,index) =>{
						lists.push({
							id:item.id,
							area_code:item.area_code,
							name:item.area_name
						})
					})
					for (var i = 0; i < lists.length; i++){
						that.array2.push(lists[i])
					}
				},error=>{})
			},
			bindPickerChange2: function(e) {
				var that = this;
				this.index2 = e.detail.value
				var lists = []
				this.array3 = [
					{name:'请选择你所属的镇(选填)',"id":0},
				]
				this.index3 = 0;
				let param={
					"user_id":this._$token.id,
					"user_token":this._$token.token,
					"type":4,
					"area_code":this.array2[this.index2].area_code
				};
				this._$InitRequest.request('index/get_city', param).then(res => {
					res.data.data.forEach((item,index) =>{
						lists.push({
							id:item.id,
							code:item.street_code,
							name:item.street_name
						})
					})
					for (var i = 0; i < lists.length; i++){
						that.array3.push(lists[i])
					}
				},error=>{})
			},
			bindPickerChange3: function(e) {
				this.index3 = e.detail.value
				var that = this;
				var lists = []
				let param={
					"user_id":this._$token.id,
					"user_token":this._$token.token,
					"type":4,
					"area_code":this.array3[this.index3].code
				};
				this._$InitRequest.request('index/get_city', param).then(res => {
					res.data.data.forEach((item,index) =>{
						lists.push({
							id:item.id,
							street_code:item.street_code,
							name:item.street_name
						})
					})
					for (var i = 0; i < lists.length; i++){
						that.array3.push(lists[i])
					}
				},error=>{})
			},
			getCity(){
				var that = this;
				var city_level = 0;
				var city_code = ""
				if( this.array[this.index].id == 0 ){
					uni.showToast({icon:"none",title:"请选择市"})
					return;
				}
				if( this.array3[this.index3].id !== 0 ){
					city_level = 1
					city_code = this.array3[this.index3].id
				}else if( this.array2[this.index2].id !== 0 ){
					city_level = 2
					city_code = this.array2[this.index2].id
				}else if( this.array[this.index].id !== 0 ){
					city_level = 3
					city_code = this.array[this.index].id
				}
				let param={
					"user_id":this._$token.id,
					"user_token":this._$token.token,
					"city_level":city_level,
					"city_id":city_code,
				};
				this._$InitRequest.request('user/binding_address', param).then(res => {
					that.addrShadows = false
					uni.showToast({icon:"none",title:res.data.mess})
				},error=>{})
			},
			thisImage(src){
				this.showShadow = true;
				this.poster = src
			},
			closeAddrShadow(){
				// this.addrShadows = false
			},
		}
	}
</script>
